/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/
button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
	border: 1px solid;
	border-color: #ccc #ccc #bbb;
	border-radius: 3px;
	background: #e6e6e6;
	color: rgba(0, 0, 0, 0.8);
	font-size: 15px;
	font-size: 0.9375rem;
	line-height: 1;
	padding: .6em 1em .4em;
}

button:hover,
input[type="button"]:hover,
input[type="reset"]:hover,
input[type="submit"]:hover {
	border-color: #ccc #bbb #aaa;
}

button:active, button:focus,
input[type="button"]:active,
input[type="button"]:focus,
input[type="reset"]:active,
input[type="reset"]:focus,
input[type="submit"]:active,
input[type="submit"]:focus {
	border-color: #aaa #bbb #bbb;
}

button[type="submit"].peak-button,
input[type="submit"].peak-button,
.peak-button {
	clear: both;
	background-color: #338c5f;
	border: 0 none;
	border-radius: 4px;
	transition: all 0.23s ease-in-out 0s;
	color: #EAF1F7;
	cursor: pointer;
	display: inline-block;
	font-size: 1em;
	font-weight: normal;
	min-height: 1em;
	line-height: 1em;
	margin: 0 5px 10px 0;
	margin: 1.5rem 0 0;
	padding: 0.75em 1.25em;
	text-align: center;
	text-decoration: none;
	vertical-align: top;
	white-space: nowrap;
	width: auto;
	vertical-align: middle;
	-webkit-transform: perspective(1px) translateZ(0);
	transform: perspective(1px) translateZ(0);
	box-shadow: 2px 4px 5px rgba(0, 0, 0, 0.2);
	position: relative;
	-webkit-transition-property: color;
	transition-property: color;
	-webkit-transition-duration: 0.3s;
	transition-duration: 0.3s;
}

button[type="submit"].peak-button::before,
input[type="submit"].peak-button::before,
.peak-button::before {
	content: "";
	position: absolute;
	z-index: -1;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	background: #2098D1;
	-webkit-transform: scaleX(0);
	transform: scaleX(0);
	-webkit-transform-origin: 50%;
	transform-origin: 50%;
	-webkit-transition-property: transform;
	transition-property: transform;
	-webkit-transition-duration: 0.3s;
	transition-duration: 0.3s;
	-webkit-transition-timing-function: ease-out;
	transition-timing-function: ease-out;
}

button[type="submit"].peak-button:hover, button[type="submit"].peak-button:focus,
input[type="submit"].peak-button:hover,
input[type="submit"].peak-button:focus,
.peak-button:hover,
.peak-button:focus {
	color: white;
}

button[type="submit"].peak-button:hover:before, button[type="submit"].peak-button:focus:before, button[type="submit"].peak-button:active:before,
input[type="submit"].peak-button:hover:before,
input[type="submit"].peak-button:focus:before,
input[type="submit"].peak-button:active:before,
.peak-button:hover:before,
.peak-button:focus:before,
.peak-button:active:before {
	-webkit-transform: scaleX(1);
	transform: scaleX(1);
}

button[type="submit"].peak-button:visited,
input[type="submit"].peak-button:visited,
.peak-button:visited {
	color: #EAF1F7;
}

