/**
 * Add padding to anchored bookmarks to account for fixed header + some margin
 */
h2, p {
	position: relative;
}

h2 span[class="anchored-header-target"], p span[class="anchored-header-target"] {
	display: block;
	position: absolute;
	top: -81px;
	padding-bottom: 81px;
	outline: none;
	border-width: 0;
}

h2 span[class="anchored-header-target"]:active, h2 span[class="anchored-header-target"]:focus, p span[class="anchored-header-target"]:active, p span[class="anchored-header-target"]:focus {
	outline: none;
	border-width: 0;
}

