	color: #7D7D7D;
}

.widget_categories .children {
	padding-top: 0;
}

/* Search widget. */
.widget_search .search-submit {
	display: none;
}

/* Pages and Menu widgets */
.widget_pages .children,
.widget_nav_menu .sub-menu {
	padding-top: 0;
	padding-bottom: .5em;
}

.widget_pages a,
.widget_nav_menu a {
	display: block;
	padding: 1em 0;
	text-decoration: none;
	border-bottom: 1px dashed gray;
}

.widget_pages a:hover, .widget_pages a:focus,
.widget_nav_menu a:hover,
.widget_nav_menu a:focus {
	text-decoration: none;
	border-bottom: 1px dashed gray;
}

.widget_pages li,
.widget_nav_menu li {
	padding: 0;
}

/* Meta widget */
.widget_meta li {
	padding: 0;
}

/* Recent comments */
.widget_recent_comments li {
	line-height: 1.3em;
	padding-bottom: .5em;
}

.widget_recent_comments a:hover,
.widget_recent_comments a:focus {
	padding-left: 0;
}

.widget_recent_entries ul {
	margin-left: 0;
	padding: 0;
	list-style-type: initial;
}

/* RSS widget */
.widget_rss li {
	margin-bottom: 1em;
}

.widget li a.rsswidget {
	padding-right: .5em;
	font-size: 1em;
	line-height: 1.4em;
}

.rss-date,
.widget_rss cite {
	color: #333;
	font-size: .8em;
}

.rssSummary {
	padding: .5em 0;
	font-size: .8em;
	line-height: 1.4em;
}

/* Search widget */
.widget_search input[type="search"] {
	box-sizing: border-box;
	width: 100%;
	padding: .5em 1em;
	border: 1px solid #c3c3c3;
	border-radius: 0;
}

/* Calendar widget */
.widget_calendar {
	font-weight: 400;
}

.widget_calendar caption {
	padding: .5em 0;
	font-size: 1em;
	font-weight: 500;
	text-align: left;
}

.widget_calendar thead {
	background: transparent;
	color: black;
}

.widget_calendar thead th {
	border-bottom-width: 2px;
}

.widget_calendar td {
	padding: .2em;
	font-size: .8em;
	text-align: center;
	background: white;
	border: 1px solid #cccccc;
}

.widget_calendar th {
	border: none;
	text-align: center;
}

.widget_calendar .pad {
	background: #eaeaea;
}

.widget_calendar a {
	font-weight: 900;
	text-decoration: none;
}

/* Text widget */
/* MailChimp signup form */
.widget #mc_embed_signup p {
	margin: 0.5em 0 1em;
}

.widget #mc_embed_signup .indicates-required {
	display: none;
}

.widget #mc_embed_signup .mc-field-group {
	clear: left;
	position: relative;
	width: 100%;
	padding-bottom: 3%;
	min-height: 50px;
}

.widget #mc_embed_signup .mc-field-group .asterisk {
	color: red;
}

.widget #mc_embed_signup .mc-field-group label {
	display: block;
	font-weight: bold;
	font-size: 85%;
	margin-bottom: 0.25em;
}

.widget #mc_embed_signup .mc-field-group input {
	display: block;
	width: 100%;
	padding: 8px 0;
	text-indent: 2%;
}

.widget #mc_embed_signup #mce-responses .response {
	margin-bottom: .75em;
}

@media (min-width: 75em) {
	.widget #mc_embed_signup .mc-field-group input,
	.widget #mc_embed_signup div.mce_inline_error {
		width: 96%;
	}
}

/* Footer widget styles */
.footer-widgets {
	position: relative;
	z-index: 1;
	margin: 2em 0 0;
	padding: 1em 0;
	background-color: #385f7e;
	background: linear-gradient(to right, #385f7e, #213647);
	color: #f3f3f3;
}

.footer-widgets .widget {
	margin: 0 1em 1em;
	padding-top: 1em;
	/**
          * Show list items on 'Recent Posts' and other specific list-type footer widgets
          */
}

.footer-widgets .widget .widget-title {
	color: #EAF1F7;
}

.footer-widgets .widget.widget_recent_entries ul > li {
	display: list-item;
	list-style: disc inside;
}

.footer-widgets a {
	color: white;
	padding: 0;
}

@media (min-width: 48em) {
	.footer-widgets {
		padding: 2em 1em;
		display: flex;
		flex-wrap: wrap;
	}
	.footer-widgets .widget {
		flex: 1 0 45%;
	}
}

@media (min-width: 75em) {
	.footer-widgets {
		flex-wrap: nowrap;
		justify-content: space-around;
		justify-content: center;
		/* If only one widget is displayed, make it full width. */
	}
	.footer-widgets .widget {
		flex: 1 1 auto;
		flex: 0 1 40%;
	}
	.footer-widgets .widget:only-of-type {
		width: 100%;
	}
	.footer-widgets .widget:first-child {
		flex-basis: 40%;
	}
}

/* connect with us menu */
ul.social-media-menu {
	margin-left: 0;
	padding-top: 0;
}

ul.social-media-menu li {
	display: inline-block;
	line-height: 1;
	font-size: 200%;
	margin-right: 0;
	padding: 0;
}

ul.social-media-menu li a {
	border-width: 0;
	padding: 0 0.25em;
	line-height: normal;
}

#secondary.widget-area {
	padding: 0 1em;
}

#secondary.widget-area .widget_recent_entries ul > li {
	display: list-item;
	list-style: disc inside;
	line-height: 1.25;
	margin-bottom: 0.75em;
}

