body,
button,
input,
select,
optgroup,
textarea {
	color: $color__text-main;
	font-family: $font__main;
	@include font-size(.9);
	line-height: $line-height;
        
        /* Media Breakpoints for Responsive Typography */
        @media #{$tablet__breakpoint} {
            @include font-size(1);
        }
        
        @media #{$desktop__breakpoint} {
            @include font-size(1.125);
        }
        
        @media #{$lg-desktop__breakpoint} {
            @include font-size(1.250);
        }
        
/*        h2,
        h3,
        h4 {
            color: $hero-dark;
        }*/
}

@import "headings";

@import "copy";
